// app/components/DashboardLayout.jsx
'use client';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { FaChartPie } from 'react-icons/fa';

const DashboardLayout = ({ children }) => {
  
  const pathname = usePathname();

  const linkClass = (path) =>
    pathname === path
      ? "flex items-center gap-2 text-white bg-blue-400 px-4 py-2 rounded-md font-semibold"
      : "flex items-center gap-2 text-gray-700 hover:text-blue-500 px-4 py-2 rounded-md";

  return (
    <div className="drawer lg:drawer-open bg-blue-100">
      <input id="my-drawer-2" type="checkbox" className="drawer-toggle" />
      
      {/* Drawer content */}
      <div className="drawer-content flex flex-col">
        {/* Navbar for small screens */}
        <div className="navbar bg-base-300 w-full lg:hidden">
          <div className="flex-none lg:hidden">
            <label htmlFor="my-drawer-2" aria-label="open sidebar" className="btn btn-square btn-ghost">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                className="inline-block h-6 w-6 stroke-current"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16"></path>
              </svg>
            </label>
          </div>
          <div className="mx-2 flex-1 px-2">Dashboard</div>
        </div>

        {/* Page content */}
        <div className="min-h-screen">
          {children}
        </div>
      </div>

      {/* Drawer sidebar */}
      <div className="drawer-side">
        <label htmlFor="my-drawer-2" aria-label="close sidebar" className="drawer-overlay"></label>

        <div className='min-h-full bg-gradient-to-t from-blue-200 to-blue-100 px-8 pt-10'>
          <Link href='/' className='flex justify-center items-center mb-8'>
            <img className='w-12 h-12' src='/Chatorbit.png' alt='Logo' />
            <span className="text-2xl text-black font-bold tracking-wider ml-2">Chatorbit</span>
          </Link>

          <ul className="menu bg-white text-base-content w-80 p-4 gap-4 border-r border-blue-300 mt-30 rounded-2xl">

            <li>
              <Link href='/dashboard' className={linkClass('/dashboard')}>
                <FaChartPie /> Overview
              </Link>
            </li>
            
          </ul>
        </div>
      </div>
    </div>
  );
};

export default DashboardLayout;
